.gallery {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 3rem;
}

.gallery-item {
    position: relative;
    max-width: 280px;
    flex: 1 0 22rem;
    color: #fff;
    cursor: pointer;
}

.gallery-item:hover .gallery-item-info,
.gallery-item:focus .gallery-item-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}

.gallery-item-info {
    display: none;
}

.gallery-item-info span {
    display: inline-block;
    font-size: 1.7rem;
    font-weight: 600;
}

.gallery-item-likes {
    margin-right: 2.2rem;
}

.gallery-item-type {
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2.5rem;
    text-shadow: 0.2rem 0.2rem 0.2rem rgba(0, 0, 0, 0.1);
}
.fa-comment {
    text-align: center;
}
.fa-heart{
    width: 80%;
    text-align: right;
}

.gallery-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}